body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

body {
	line-height: 1;
}

ol,
ul {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after {
	content: '';
	content: none;
}

q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
	box-sizing: border-box;
}

a {
	text-decoration: none;
	color: inherit;
}

html {
	font-size: 62.5%;
}

body {
	font-size: 1.6rem;
	font-family: 'Roboto', sans-serif;
}

.container {
	width: 85%;
	max-width: 1180px;
	margin: 0 auto;
}

.title {
	font-size: 3rem;
	margin: 3rem 0;
}

.subtitle {
	font-size: 2.4rem;
	margin: 2.4rem 0;
	font-family: 'Oswald', sans-serif;
}

.btn {
	background-color: #ce3335;
	padding: 1rem;
	color: #ffffff;
	border: 0.1rem solid #ce3335;
	border-radius: 0.5rem;
	cursor: pointer;
	box-shadow: rgba(0, 0, 0, 0.2) 0 3px 6px;
	min-height: 4.8rem;
}

.btn.loading::after {
	display: inline-block;
}

.btn::after {
	content: '';
	display: none;
	color: #ffffff;
	font-size: 0.3rem;
	margin: 1rem 2rem;
	width: 1em;
	height: 1em;
	border-radius: 50%;
	position: relative;
	text-indent: -9999em;
	-webkit-animation: load4 1.3s infinite linear;
	        animation: load4 1.3s infinite linear;
	transform: translateZ(0);
}
@-webkit-keyframes load4 {
	0%,
	100% {
		box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
	}
	12.5% {
		box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
	}
	25% {
		box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0,
			0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
	}
	37.5% {
		box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em,
			0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
	}
	50% {
		box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em,
			0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
	}
	62.5% {
		box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em,
			0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
	}
	75% {
		box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
	}
	87.5% {
		box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
	}
}
@keyframes load4 {
	0%,
	100% {
		box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
	}
	12.5% {
		box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
	}
	25% {
		box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0,
			0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
	}
	37.5% {
		box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em,
			0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
	}
	50% {
		box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em,
			0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
	}
	62.5% {
		box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em,
			0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
	}
	75% {
		box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
	}
	87.5% {
		box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em,
			0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
	}
}

.btn--secondary {
	color: #ce3335;
	background-color: #ffffff;
}

.btn--secondary::after {
	color: #ce3335;
}

.btn-center {
	margin: 2rem auto;
	display: block;
}

.input {
	width: 100%;
	padding: 1rem;
	border-radius: 0.5rem;
	background-color: #ffffff;
	font-size: 1.6rem;
	margin: 1rem 0 0;
}

.error-message {
	display: block;
	color: #ce3335;
	margin-bottom: 1.5rem;
	margin-top: 0.5rem;
}

.input + .label {
	margin-top: 1.5rem;
}

.label {
	font-size: 1.6rem;
}

.tabs {
	display: flex;
}

.tabs .tab {
	flex: 1 1;
	text-align: center;
	border-radius: 2rem 2rem 0 0;
	padding: 2rem 0;
	background-color: #f5f5f5;
	margin-bottom: 0;
	cursor: pointer;
}

.tabs .tab.tab--active {
	box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 6px;
	background-color: #ce3335;
	color: #ffffff;
}

.tabs-body {
	background-color: #f5f5f5;
	border-radius: 0 0 1rem 1rem;
	padding: 3rem;
}

.header {
	color: #ffffff;
	background-color: #ce3335;
	padding: 2rem 0;
	margin-bottom: 2rem;
}

.header .container {
	display: flex;
	justify-content: space-between;
}

.nav .nav-link {
	margin-left: 2rem;
	cursor: pointer;
}

.login {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.login .login__img {
	width: 50rem;
	height: 50rem;
	margin-right: 10rem;
	background-image: url(/NewsApp-React/static/media/news_presenter.c1df5130.svg);
}

.login .login__form {
	flex: 1 1;
}

.posts {
	display: grid;
	grid-gap: 2rem;
	gap: 2rem;
	grid-template-columns: repeat(3, 1fr);
	margin-bottom: 2rem;
}

.post {
	position: relative;
	overflow: hidden;
	background-color: #f5f5f5;
	box-shadow: rgba(0, 0, 0, 0.2) 0 3px 6px;
	border-radius: 1rem;
	display: flex;
	flex-direction: column;
}

.post.post--justify {
	justify-content: space-between;
}

.post .post__img {
	height: 20rem;
	display: flex;
	align-items: center;
	overflow: hidden;
}

.post .post__img img {
	width: 100%;
}

.post .post__text {
	padding: 2rem;
}

.post .post__date {
	font-size: 1.4rem;
	margin-top: 0.5rem;
}

.post .post__content {
	line-height: 2.5rem;
}

.post .post__overlay {
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	transition: 0.2s;
}

.post:hover .post__overlay {
	opacity: 1;
}

.search-row {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.search-row .input-icon {
	width: 40%;
	position: relative;
}

.search-row .input {
	border-radius: 2rem;
	margin: 0;
	border: 0.2rem solid #ce3335;
}

.search-row .input:focus {
	outline: 0;
}

.search.icon {
	color: #ce3335;
	position: absolute;
	top: 1rem;
	right: 2rem;
	margin-top: 2px;
	margin-left: 3px;
	width: 12px;
	height: 12px;
	border: solid 2px currentColor;
	border-radius: 100%;
	transform: rotate(-45deg);
}
.search.icon:before {
	content: '';
	position: absolute;
	top: 10px;
	left: 4px;
	height: 6px;
	width: 2px;
	background-color: currentColor;
}

/*
 * Note that this is toastr v2.1.3, the "latest" version in url has no more maintenance,
 * please go to https://cdnjs.com/libraries/toastr.js and pick a certain version you want to use,
 * make sure you copy the url from the website since the url may change between versions.
 * */
.toast-title {
	font-weight: 700;
}
.toast-message {
	-ms-word-wrap: break-word;
	word-wrap: break-word;
}
.toast-message a,
.toast-message label {
	color: #fff;
}
.toast-message a:hover {
	color: #ccc;
	text-decoration: none;
}
.toast-close-button {
	position: relative;
	right: -0.3em;
	top: -0.3em;
	float: right;
	font-size: 20px;
	font-weight: 700;
	color: #fff;
	-webkit-text-shadow: 0 1px 0 #fff;
	text-shadow: 0 1px 0 #fff;
	opacity: 0.8;
	-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
	filter: alpha(opacity=80);
	line-height: 1;
}
.toast-close-button:focus,
.toast-close-button:hover {
	color: #000;
	text-decoration: none;
	cursor: pointer;
	opacity: 0.4;
	-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
	filter: alpha(opacity=40);
}
.rtl .toast-close-button {
	left: -0.3em;
	float: left;
	right: 0.3em;
}
button.toast-close-button {
	padding: 0;
	cursor: pointer;
	background: 0 0;
	border: 0;
	-webkit-appearance: none;
}
.toast-top-center {
	top: 0;
	right: 0;
	width: 100%;
}
.toast-bottom-center {
	bottom: 0;
	right: 0;
	width: 100%;
}
.toast-top-full-width {
	top: 0;
	right: 0;
	width: 100%;
}
.toast-bottom-full-width {
	bottom: 0;
	right: 0;
	width: 100%;
}
.toast-top-left {
	top: 12px;
	left: 12px;
}
.toast-top-right {
	top: 12px;
	right: 12px;
}
.toast-bottom-right {
	right: 12px;
	bottom: 12px;
}
.toast-bottom-left {
	bottom: 12px;
	left: 12px;
}
#toast-container {
	position: fixed;
	z-index: 999999;
	pointer-events: none;
}
#toast-container * {
	box-sizing: border-box;
}
#toast-container > div {
	position: relative;
	pointer-events: auto;
	overflow: hidden;
	margin: 0 0 6px;
	padding: 15px 15px 15px 50px;
	width: 300px;
	border-radius: 3px;
	background-position: 15px center;
	background-repeat: no-repeat;
	box-shadow: 0 0 12px #999;
	color: #fff;
	opacity: 0.8;
	-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
	filter: alpha(opacity=80);
}
#toast-container > div.rtl {
	direction: rtl;
	padding: 15px 50px 15px 15px;
	background-position: right 15px center;
}
#toast-container > div:hover {
	box-shadow: 0 0 12px #000;
	opacity: 1;
	-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
	filter: alpha(opacity=100);
	cursor: pointer;
}
#toast-container > .toast-info {
	background-image: url(data:image/png;base64,) !important;
}
#toast-container > .toast-error {
	background-image: url(data:image/png;base64,) !important;
}
#toast-container > .toast-success {
	background-image: url(data:image/png;base64,) !important;
}
#toast-container > .toast-warning {
	background-image: url(data:image/png;base64,) !important;
}
#toast-container.toast-bottom-center > div,
#toast-container.toast-top-center > div {
	width: 300px;
	margin-left: auto;
	margin-right: auto;
}
#toast-container.toast-bottom-full-width > div,
#toast-container.toast-top-full-width > div {
	width: 96%;
	margin-left: auto;
	margin-right: auto;
}
.toast {
	background-color: #030303;
}
.toast-success {
	background-color: #51a351;
}
.toast-error {
	background-color: #bd362f;
}
.toast-info {
	background-color: #2f96b4;
}
.toast-warning {
	background-color: #f89406;
}
.toast-progress {
	position: absolute;
	left: 0;
	bottom: 0;
	height: 4px;
	background-color: #000;
	opacity: 0.4;
	-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
	filter: alpha(opacity=40);
}
@media all and (max-width: 240px) {
	#toast-container > div {
		padding: 8px 8px 8px 50px;
		width: 11em;
	}
	#toast-container > div.rtl {
		padding: 8px 50px 8px 8px;
	}
	#toast-container .toast-close-button {
		right: -0.2em;
		top: -0.2em;
	}
	#toast-container .rtl .toast-close-button {
		left: -0.2em;
		right: 0.2em;
	}
}
@media all and (min-width: 241px) and (max-width: 480px) {
	#toast-container > div {
		padding: 8px 8px 8px 50px;
		width: 18em;
	}
	#toast-container > div.rtl {
		padding: 8px 50px 8px 8px;
	}
	#toast-container .toast-close-button {
		right: -0.2em;
		top: -0.2em;
	}
	#toast-container .rtl .toast-close-button {
		left: -0.2em;
		right: 0.2em;
	}
}
@media all and (min-width: 481px) and (max-width: 768px) {
	#toast-container > div {
		padding: 15px 15px 15px 50px;
		width: 25em;
	}
	#toast-container > div.rtl {
		padding: 15px 50px 15px 15px;
	}
}

